@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css);
@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Madimi+One&display=swap);
body {
  margin: 0;
  font-family: "Lato", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.component-Container{
  margin: 48px auto;
  width: 80%;
}

*{
  margin: 0px;
  padding: 0px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navbar{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    background-color: white;
    display: flex;
    justify-content: space-around;
    padding: 12px 0px;
    box-shadow: 0 1px 3px -1px  black;
    z-index: 2;
}

.nav-logo img{
    height: 58px;
}

.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    grid-gap: 48px;
    gap: 48px;
    font-size: 16px;
    font-weight: 500;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
    color: green;
    cursor: pointer;
}

.nav-menu li hr{
    height: 2px;
    width: 80%;
    border: none;
    background-color: red;
    border-radius: 1rem;
}

.nav-login-cart{
    display: flex;
    align-items: center;
    grid-gap: 32px;
    gap: 32px;
}

.nav-login-cart button{
    height: 32px;
    width: 72px;
    border: 1px solid green;
    border-radius: 22px;
    background-color: white;
    font-size: 16px;
    font-weight: 500;
    color: gray;
    cursor: pointer;
}

.nav-login-cart button:active{
    background-color: antiquewhite;
}

.nav-cart{
    display: flex;
}

.nav-login-cart .nav-cart img{
    height: 28px;
}
.cart-count{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    background-color: red;
    color: white;
    font-size: 12px;
    border-radius: 50%;
    margin: -10px -9px;
}
.Hero{
    height: 100vh;
    background-image: linear-gradient(rgba(255, 0, 0, 0.267),#fff);
    display: flex;
    justify-content: center;
    font-family: "Kanit", sans-serif;
}
.Hero-left{
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: end;
    flex-direction: column;
    grid-gap: 18px;
    gap: 18px;
}
.hand-icon{
    display: flex;
    grid-gap: 18px;
    gap: 18px;
    align-items: center;
    justify-content:center;
}
.Hero-left button{
    padding: 12px 22px;
    background-color: transparent;
    border: 2px solid green;
    border-radius: 16px;
    cursor: pointer;
    font-size: medium;
    font-weight: 600;
}
.slogan-hero p{
    font-size: 32px;
    text-align: center;
    font-weight: 300;
}   
.slogan-hero img{
    height: 44px;
}
.Hero-right {
    flex: 1 1;
    display: flex;
    justify-content: center;
}
.Hero-right img{
    height: 90%;
}

.Item-product{
    cursor: pointer;
    font-family: "Lato", sans-serif;
    padding: 12px;
    /* border: 1px solid green; */
    width: 232px;
    height: auto;
    grid-gap: 12px;
    gap: 12px;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: 0px 5px 10px rgba(255, 0, 0, 0.267);
}

.Item-product p{
    font-size: 16px;
    font-weight: 400;
    color: rgb(90, 90, 90);
}
.Item-product img{
    border-radius: 10px;
    border: 1px solid gray;
}
.price-btn{
    display: flex;
    justify-content: space-between;
}
.price-btn div{
    display: flex;
    align-items: center;
}
.price-btn p{
    color: black;
    text-decoration: line-through;
    font-weight: 500;
}
.price-btn .new-price{
    font-size: 22px;
    font-weight: 600;
    margin-left: 12px;
    text-decoration: none;
}
.price-btn button{
    padding: 6px 12px;
    border: none;
    background-color: rgba(28, 173, 28, 0.829);
    color: lavenderblush;
    font-size: 14px;
    font-weight: 500;
    border-radius: 8px;
    cursor: pointer;
}

.Item-product:hover {
    transform: scale(1.050);
    transition: .3s;
}
.popular-container{
    height: auto;
    margin-bottom: 64px;
}
.popular-items{
    display: flex;
    grid-gap: 12px;
    gap: 12px;
    margin-top: 32px;
    justify-content: center;
}

.heading-card h1{
    text-align: center;
    font-size: 34px;
    font-weight: 400;
    color:black;
    text-shadow: 4px 4px 10px green;
    color: #fff;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
}
.heading-card hr{
    width: 50%;
    text-align: center;
    height: 4px;
    border: none;
    border-radius: 32px;
    background-color: rgba(0, 128, 0, 0.699);
}
.heading-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 18px;
    gap: 18px;
    font-family: "Madimi One", sans-serif;
    margin: 32px 0px;
}

.offer-conatainer{
    height: 45vh;
    background: linear-gradient(to left, white, rgba(4, 163, 4, 0.521));
    display: flex;
    justify-content: space-around;
    box-shadow: 10px 10px 10px rgba(255, 0, 0, 0.267);
    cursor: pointer;
    transition: 3ms;
}
.offer-left{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    grid-gap: 32px;
    gap: 32px;
    font-size: 56px;
    font-weight: 600;
    color: whitesmoke;
}
.offer-left p{
    text-shadow: 1px 1px rgba(4, 163, 4, 0.521)
}

.offer-left p span{
    color: purple;
    font-weight: 700;
}

.offer-right{
    display: flex;
    justify-content: center;
    align-items: center;
}
.offer-right img{
    display: block;
    height: 120%;
}
.offer-conatainer:hover{
    box-shadow: 15px 15px 20px rgba(255, 0, 0, 0.267);
}
.colletion-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 18px;
    gap: 18px;
    margin: 32px 0px;
}
.colletion-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 18px;
    gap: 18px;
    margin: 32px 0px;
}
.category-banner{
    width: 100%;
    background-color: aqua;
    box-shadow: 5px 5px 10px orange;
    margin-bottom: 32px;
}
.log-in-form{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
        "firstName Lastname"
        "Email Email"
        "setPassword confirmPassword"
        "terms terms"
    ;
    grid-gap: 22px;
    gap: 22px;
}
.form-container{
    width: 60%;
    padding: 32px;
    box-shadow: 15px 15px 30px green;
    display: flex;
    flex-direction: column;
    grid-gap: 32px;
    gap: 32px;
    border: 1px solid green;
    border-radius: 12px;
}
.login-singup-container{
    display: flex;
    justify-content: center;
}
.login-firstName{
    grid-area: firstName;   
}
.login-lastName{
    grid-area: firstName;   
}
.login-email-field{
    grid-area: Email; 
}
.login-setPassword{
    grid-area: setPassword;
}
.login-confirmPassword{
    grid-area: confirmPassword;
}
.log-in-form div{
    display: flex;
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;

}
.log-in-form div label{
    font-size: 18px;
    font-weight: 500;
}
.log-in-form div input{
    padding: 8px;
    border: 1px solid grey;
    border-radius: 8px;
}
.accept-temrs{
    grid-gap: 0px;
    gap: 0px;
    flex-direction: row;
    grid-area: terms;
}
.accept-temrs input{
    width: 12px;
}
.log-in-form p{
    font-size: 16px;
}
.log-in-form p span{
    font-weight: 600;
    cursor: pointer;
}
.log-in-form button{
    padding: 6px 12px;
    border: none;
    background-color: rgba(28, 173, 28, 0.829);
    color: lavenderblush;
    font-size: 14px;
    font-weight: 500;
    border-radius: 8px;
    cursor: pointer;
}
.news-letter{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35vh;
    background-image: url(/github.io/ECOMMERCE-1/static/media/news_letter.11536906.jpg);
    background-size: cover;
    background-position: center;
    border: 1px solid green;
    border-radius: 12px;
    grid-gap: 32px;
    gap: 32px;
}
.news-letter-left p{
    font-size: 22px;
    font-weight: 500;
}
.news-letter-right{
    display: flex;
    flex-direction: column;
    grid-gap: 16px;
    gap: 16px;
}

.news-letter-left{
    display: flex;
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;
}
.news-letter-left h1{
    font-weight: 600;
}

.news-letter-right label{
    font-size: 16px;
    font-weight: 600;
}
.news-letter-right input{
    border: none;
    padding: 8px 22px;
    font-size: 18px;
    background: white;
    color: green;
    border-radius: 5px;
}
.news-letter-right button{
    background-color: rgba(28, 173, 28, 0.829);
    padding: 10px 24px;
    border: none;
    border-radius: 12px;
    color: #fff;
}
.footer{
    background: linear-gradient(white, rgba(28, 173, 28, 0.829));
    padding: 32px 32px 1px 32px;
}
.footer-tems{
    display: flex;
    flex-direction: column;
    grid-gap: 6px;
    gap: 6px;
}
.footer-tems a{
    text-decoration: none;
    color: black;
    cursor: pointer;
}
.footer-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom: 18px;
}
.tems-condition{
    text-align: center;
    margin: 12px;
    color: black;
    font-family: 'roboto';
    font-weight: 500;
}
.social-icon li{
    list-style: none;
    margin-bottom: 6px;
}
.social-icon {
    padding: 0px;
}
.social-icon h1{
    font-size: 28px;
    margin-bottom: 12px;
}
.social-icon li  i{
    margin-right: 12px;
}

